import { Component, Input, OnInit } from '@angular/core';

@Component({
  selector: 'app-titulo',
  templateUrl: './titulo.component.html',
  styleUrls: ['./titulo.component.css']
})
export class TituloComponent implements OnInit {
  @Input() titulo:string;
  @Input() color:string="rgb(4, 4, 88)";
  @Input() id:string;
  
  constructor() { }

  ngOnInit() {

  }
  ngAfterViewChecked() {
      
    let tituloel = document.getElementById("cont-titulo"+ this.id)
    let border = document.getElementById("border"+ this.id)
    tituloel.style.color= this.color;
    border.style.backgroundColor= this.color;
  }

}
